import Cookies from 'js-cookie';

export const isAuthenticate = () => {
    let strRefreshToken = Cookies.get('strRefreshToken');
    let strAccessToken = Cookies.get('strAccessToken');
    let strUserId = Cookies.get('strUserId');
    let strUserName = Cookies.get('strUserName');
    if (strRefreshToken && strAccessToken)
        return {
            strRefreshToken,
            strAccessToken,
            strUserId,
            strUserName
        };

    return null;
};
