import React, {lazy} from 'react';
import {Route, Switch, useLocation} from 'react-router-dom';

import MainLayout from './../layout/MainLayout';
import ProtectedRoute from './ProtectedRoute';
const DashboardDefault = lazy(() => import('../views/dashboard/Default'));

// const TableBasic = lazy(() => import('../views/forms/tables/TableBasic'));
// const TableDense = lazy(() => import('../views/forms/tables/TableDense'));

const category = lazy(() => import('../views/category/Category'));
const subCategory = lazy(() => import('../views/subCategory/SubCategory'));
const addProduct = lazy(() => import('../views/addProduct/AddProduct'));
const colours = lazy(() => import('../views/colour/Colour'));
const sizes = lazy(() => import('../views/size/Size'));

const editProduct = lazy(() => import('../views/editProduct/EditProduct'));
const viewProducts = lazy(() => import('../views/viewProducts/ViewProducts'));

// const UtilsTypography = lazy(() => import('../views/utilities/typography'));
// const UtilsColor = lazy(() => import('../views/utilities/color'));
// const UtilsShadow = lazy(() => import('../views/utilities/shadow'));
// const UtilsMaterialIcons = lazy(() => import('../views/utilities/icons/MaterialIcons'));
// const UtilsTablerIcons = lazy(() => import('../views/utilities/icons/TablerIcons'));

// const SamplePage = lazy(() => import('../views/sample-page'));

const MainRoutes = () => {
    const location = useLocation();
    return (
        <Route
            path={[
                '/dashboard',

                '/category',
                '/subCategory',
                '/addProduct',
                '/viewProducts',
                '/Colours',
                '/Sizes',
                '/viewProducts',
                '/editProduct/:productId'

                // '/tables/tbl-basic',
                // '/tables/tbl-dense',

                // '/utils/util-typography',
                // '/utils/util-color',
                // '/utils/util-shadow',
                // '/icons/tabler-icons',
                // '/icons/material-icons',

                // '/sample-page'
            ]}
        >
            <MainLayout showBreadcrumb={true}>
                <Switch location={location} key={location.pathname}>
                    <ProtectedRoute path="/dashboard" component={DashboardDefault} />

                    <ProtectedRoute path="/category" component={category} />
                    <ProtectedRoute path="/subCategory" component={subCategory} />
                    <ProtectedRoute path="/addProduct" component={addProduct} />
                    <ProtectedRoute path="/Colours" component={colours} />
                    <ProtectedRoute path="/Sizes" component={sizes} />

                    <ProtectedRoute path="/editProduct/:productId" component={editProduct} />
                    <ProtectedRoute path="/viewProducts" component={viewProducts} />
                    {/* 
                    <Route path="/tables/tbl-basic" component={TableBasic} />
                    <Route path="/tables/tbl-dense" component={TableDense} />

                    <Route path="/utils/util-typography" component={UtilsTypography} />
                    <Route path="/utils/util-color" component={UtilsColor} />
                    <Route path="/utils/util-shadow" component={UtilsShadow} />
                    <Route path="/icons/tabler-icons" component={UtilsTablerIcons} />
                    <Route path="/icons/material-icons" component={UtilsMaterialIcons} />

                    <Route path="/sample-page" component={SamplePage} /> */}
                </Switch>
            </MainLayout>
        </Route>
    );
};

export default MainRoutes;
